}

img.smokeEffect{
  position: relative;
  top: 40%;
  left: 20%;
  width: 30vw;
  -webkit-animation-name: makeSmoke;
          animation-name: makeSmoke;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-transition: all 1.5s;
  transition: all 1.5s;
  opacity: 0;

}

@-webkit-keyframes makeSmoke {
  0%{
                 -webkit-transform:
                 translateX(5%) rotate(45deg);
                         transform:
                 translateX(5%) rotate(45deg);
                 -webkit-filter: blur(3px);
                         filter: blur(3px);
                 opacity: 0;
                 width: 10vw}

  25%{
                 opacity: 0.2;
                 -webkit-filter: drop-shadow(4px 4px 4px #000);
                         filter: drop-shadow(4px 4px 4px #000);}
  50%{
                  -webkit-transform:
                  translateX(0%) rotate(45deg);
                          transform:
                  translateX(0%) rotate(45deg);
                  opacity: 0.3;
                  -webkit-filter: drop-shadow(4px 4px 4px #000);
                          filter: drop-shadow(4px 4px 4px #000);
                  width:20vw; }
  100%{
                  -webkit-transform: translateX(4%) rotate(45deg);
                          transform: translateX(4%) rotate(45deg);
                  opacity: 0;
                  width:30vw; }
}

@keyframes makeSmoke {
  0%{
                 -webkit-transform:
                 translateX(5%) rotate(45deg);
                         transform:
